import JoinButton from '../../../../ui/buttons/BuyButton/BuyButton';
import {TakeOutButton} from '../../../../ui/buttons/TakeOutButton/TakeOutButton';

import styles from './goldContract.module.scss';

import { ReactSVG } from 'react-svg';
import infoIcon from '../../icons/info.svg';
import indefiniteIcon from '../../icons/indefinite.svg';
import clockIcon from '../../icons/clock.svg';
import calendarIcon from '../../icons/calendar.svg';
import incomeIcon from '../../icons/income.svg';

export const GoldContract = () => {
  return (
    <div className={styles.goldBlock}>
      <div className={styles.name}>
        <p></p>
        <div className={styles.contractName}>
          <h2></h2>
          <ReactSVG className={styles.infoIcon} src={infoIcon} />
        </div>
      </div>

      <div className={styles.description}>
        <div className={styles.descriptionItem}>
          <div className={styles.iconBlock}>
            <ReactSVG className={styles.descriptionIcon} src={indefiniteIcon} />
          </div>
          <p></p>
          <ReactSVG className={styles.infoIcon} src={infoIcon} />
        </div>
        <div className={styles.descriptionItem}>
          <div className={styles.iconBlock}>
          <ReactSVG className={styles.descriptionIcon} src={clockIcon} />
          </div>
          <p>   </p>
        </div>
        <div className={styles.descriptionItem}>
          <div className={styles.iconBlock}>
            <ReactSVG className={styles.descriptionIcon} src={calendarIcon} />
          </div>
          <p>  DNC  RZM</p>
          <ReactSVG className={styles.infoIcon} src={infoIcon} />
        </div>
        <div className={styles.descriptionItem}>
          <div className={styles.iconBlock}>
            <ReactSVG className={styles.descriptionIcon} src={incomeIcon} />
          </div>
          <p>  54% </p>
        </div>
      </div>

      <div className={styles.extraInfo}>
        <div className={styles.firstItem}>
          <p>  </p>
          <span> 4 </span>
        </div>
        <div className={styles.secondItem}>
          <p>- </p>
          <div>
            <span></span>
            <ReactSVG className={styles.infoIcon} src={infoIcon} />
          </div>
        </div>
      </div>

      <div className={styles.buttons}>
      <div className={styles.button}>
        <JoinButton color='gold' onClick={() => {}}>
          
        </JoinButton>
      </div>
      <div className={styles.button}>
        <TakeOutButton color={'gold'}>
          
        </TakeOutButton>
      </div>
    </div>
    </div>
  )
}
